import "./../App.css";
import "./../index.css";

import {
  Box,
  Button,
  Checkbox,
  createTheme,
  FormControlLabel,
  FormGroup,
  Grid,
  Slider,
  ThemeOptions,
  ThemeProvider,
  ToggleButtonGroup,
  Typography,
} from "@mui/material";
import { styled } from "@mui/material/styles";
import ToggleButton, { ToggleButtonProps } from "@mui/material/ToggleButton";
import { useState } from "react";

import FilteredGraph, {
  AgeFilter,
  EthnicityFilter,
  GenderFilter,
} from "../component/FilteredGraph";

type AgeRange = "5-17" | "18-24" | "25-44" | "45-64" | "65+";
type ExtendedGenderFilter = GenderFilter | "nb";

const themeOptions: ThemeOptions = {
  palette: {
    primary: {
      main: "#15B0F2",
    },
    secondary: {
      main: "#26D07C",
    },
  },
  typography: {
    fontFamily: "Overpass, sans-serif",
    fontWeightRegular: "300",
    h2: {
      fontFamily: "Chivo, sans-serif",
    },
    h6: {
      fontFamily: "Chivo, sans-serif",
      fontWeight: "300",
    },
  },
};

const theme = createTheme(themeOptions);

const BorderlessToggleButton = styled(ToggleButton)<ToggleButtonProps>(() => ({
  "&.MuiToggleButtonGroup-grouped": {
    borderStyle: "none",
    textTransform: "none",
  },
}));

interface Mark {
  value: number;
  label: AgeRange;
  filter: AgeFilter;
}

const marks: Mark[] = [
  {
    value: 0,
    label: "5-17",
    filter: "C",
  },
  {
    value: 25,
    label: "18-24",
    filter: "YA",
  },
  {
    value: 50,
    label: "25-44",
    filter: "A",
  },
  {
    value: 75,
    label: "45-64",
    filter: "MA",
  },
  {
    value: 100,
    label: "65+",
    filter: "E",
  },
];

function valueLabelFormat(value: number): string | undefined {
  return marks.find((mark) => mark.value === value)?.label;
}

export default function InteractiveChart() {
  const [ageRange, setAgeRange] = useState<number>(50);
  const [ageFilterDisabled, setAgeFilterDisabled] = useState(false);

  const [genderFilter, setGenderFilter] = useState<
    ExtendedGenderFilter | undefined
  >();
  const [ethnicityFilter, setEthnicityFilter] = useState<
    EthnicityFilter | undefined
  >();

  const handleGender = (event: any, newGender: string | null) => {
    if (newGender !== null) {
      setGenderFilter(newGender as ExtendedGenderFilter);
    }
  };

  const handleEthnicity = (event: any, newGender: string | null) => {
    if (newGender !== null) {
      setEthnicityFilter(newGender as EthnicityFilter);
    }
  };

  const handleReset = () => {
    setAgeRange(50);
    setAgeFilterDisabled(false);
    setGenderFilter(undefined);
    setEthnicityFilter(undefined);
  };

  const ageFilter: AgeFilter = ageFilterDisabled
    ? "none"
    : marks.find((mark) => mark.value === ageRange)?.filter ?? "none";

  return (
    <ThemeProvider theme={theme}>
      <div>
        <Grid container rowSpacing={1} columnSpacing={{ xs: 1, sm: 2, md: 3 }}>
          <Grid item xs={12} md={4} order={{ xs: 2, md: 1 }}>
            <Box alignItems="center" textAlign="center">
              <Typography variant="h6">Age:</Typography>
              <Slider
                defaultValue={50}
                step={null}
                valueLabelDisplay={"off"}
                valueLabelFormat={valueLabelFormat}
                marks={marks}
                disabled={ageFilterDisabled}
                value={ageRange}
                onChange={(event, newValue) => setAgeRange(newValue as number)}
                style={{
                  marginLeft: 30,
                  marginRight: 30,
                  width: "auto",
                  display: "flex",
                }}
              />
              <Box>
                <FormGroup
                  sx={{
                    "&.MuiFormGroup-root": {
                      alignItems: "center",
                    },
                  }}
                >
                  <FormControlLabel
                    control={
                      <Checkbox
                        checked={ageFilterDisabled}
                        onChange={() =>
                          setAgeFilterDisabled(!ageFilterDisabled)
                        }
                      />
                    }
                    label="Prefer not to say"
                  />
                </FormGroup>
              </Box>
            </Box>
            <Box
              display="flex"
              flexDirection="row"
              alignContent="center"
              justifyContent="space-around"
            >
              <Box flex={1} textAlign="center">
                <Typography variant="h6">Gender:</Typography>
                <ToggleButtonGroup
                  value={genderFilter}
                  exclusive
                  onChange={handleGender}
                  orientation="vertical"
                  color="primary"
                >
                  <BorderlessToggleButton value="male" size="small">
                    Male
                  </BorderlessToggleButton>
                  <BorderlessToggleButton value="female" size="small">
                    Female
                  </BorderlessToggleButton>
                  <BorderlessToggleButton value="nb" size="small">
                    Non-Binary
                  </BorderlessToggleButton>
                  <BorderlessToggleButton value="none" size="small">
                    Other / Prefer not to say
                  </BorderlessToggleButton>
                </ToggleButtonGroup>
              </Box>
              <Box flex={1} textAlign="center">
                <Typography variant="h6">Ethnicity:</Typography>
                <ToggleButtonGroup
                  value={ethnicityFilter}
                  exclusive
                  onChange={handleEthnicity}
                  orientation="vertical"
                  color="primary"
                >
                  <BorderlessToggleButton value="black" size="small">
                    Black
                  </BorderlessToggleButton>
                  <BorderlessToggleButton value="asian" size="small">
                    Asian
                  </BorderlessToggleButton>
                  <BorderlessToggleButton value="hisp" size="small">
                    Hispanic
                  </BorderlessToggleButton>
                  <BorderlessToggleButton value="white" size="small">
                    White
                  </BorderlessToggleButton>
                  <BorderlessToggleButton value="mex" size="small">
                    Mexican-American
                  </BorderlessToggleButton>
                  <BorderlessToggleButton value="none" size="small">
                    Other / Prefer not to say
                  </BorderlessToggleButton>
                </ToggleButtonGroup>
              </Box>
            </Box>
            <Box alignItems="center" textAlign="center">
              <Button variant="text" onClick={handleReset}>
                Reset
              </Button>
            </Box>
          </Grid>
          <Grid item xs={12} md={8} order={{ xs: 1, md: 2 }}>
            <FilteredGraph
              age={ageFilter}
              ethnicity={ethnicityFilter ?? "none"}
              gender={
                genderFilter === "nb" || genderFilter == null
                  ? "none"
                  : genderFilter
              }
            />
          </Grid>
        </Grid>
        <Typography style={{ margin: 10, marginTop: 20 }}>
          Phthalates are synthetic chemicals that can be harmful to health. They
          encompass a lot of different individual chemicals, and these can be
          split up into two categories (or classes), based on size. Low
          molecular weight (LMW) phthalates (like DMP, DEP, and DBP) are in many
          common personal care and beauty products. Manufacturers use them to
          create fragrances, artificially color soap, give shampoo texture, and
          more. High molecular weight (HMW) phthalates (like DEHP, DINP, and
          DOP) are used in plastic to make it flexible and durable. They can be
          found in automotive parts, building materials, food packaging, and
          more. Studies show that both classes of phthalates mess up our
          hormones. High phthalate exposure has been linked to obesity,
          infertility, cancer, and other health concerns.
        </Typography>
        {genderFilter != null && (
          <Typography style={{ margin: 10 }}>
            Women are exposed to more phthalates than men, especially non-white
            women. LMW phthalates are commonly used in beauty, personal care,
            and cleaning products. HMW phthalates are typically used in vinyl
            floor coverings, construction materials, and PVC coatings for cars.
            Phthalates mimic estrogen and can throw off hormonal balance. High
            phthalate levels have been linked to infertility in men and women.
          </Typography>
        )}
        {ethnicityFilter != null && (
          <Typography style={{ margin: 10 }}>
            Non-white people are exposed to more phthalates, with Black women
            having the highest exposures. Most non-white exposures are due to
            LMW phthalates present in anti-aging and skin lightening creams,
            hair products, and makeup. HMW phthalates used in housing materials,
            toys, and food packaging lead to high exposures in urban communities
            and food deserts.
          </Typography>
        )}
      </div>
    </ThemeProvider>
  );
}
