function renderLegendText(value: string, entry: any) {
  return <span style={{ fontFamily: "Overpass, sans-serif" }}>{value}</span>;
}

export default function FilteredGraph({
  age,
  gender,
  ethnicity,
}: FilteredGraphProps) {
  const yearlyData = Object.entries(typedJsonData)
    .map(([year, ages]) => {
      const genders = ages[age];
      const ethnicities = genders[gender];
      // need to do this modification here
      const data = ethnicities[ethnicity];
      if (!data) {
        return undefined;
      }
      if (data.low_mw_average == 0) {
        return undefined;
      }
      if (data.high_mw_average == 0) {
        return undefined;
      }
      const secondYearInt = Number.parseInt(year.substring(5), 10);
      return {
        year: secondYearInt,
        lowmol: data.low_mw_average,
        highmol: data.high_mw_average,
      };
    })
    .filter(isPresent)
    .reverse();

  return (
    <ResponsiveContainer>
      <LineChart
        width={500}
        height={300}
        data={yearlyData}
        margin={{
          top: 5,
          right: 30,
          left: 20,
          bottom: 10,
        }}
      >
        <XAxis
          dataKey="year"
          type="number"
          domain={["dataMin", "dataMax"]}
          tickCount={3}
          tickLine={false}
          tick={{ fontFamily: "Overpass, sans-serif" }}
        ></XAxis>
        <YAxis
          label={{
            value: "μg/g",
            angle: -90,
            position: "insideLeft",
            fontFamily: "Overpass, sans-serif",
            fontWeight: 300,
          }}
          tickLine={false}
          tickCount={3}
          tick={{ fontFamily: "Overpass, sans-serif" }}
        />
        <Legend
          verticalAlign="bottom"
          layout="horizontal"
          height={15}
          wrapperStyle={{ paddingTop: 20 }}
          formatter={renderLegendText}
        />
        <Line
          name="Low Molecular Weight"
          type="monotone"
          dataKey="lowmol"
          stroke="#26D07C"
          activeDot={{ r: 5 }}
          dot={false}
          strokeWidth={3}
        />
        <Line
          name="High Molecular Weight"
          type="monotone"
          dataKey="highmol"
          stroke="#15B0F2"
          activeDot={{ r: 5 }}
          dot={false}
          strokeWidth={3}
        />
        <ReferenceLine
          // label="BPA phase-out"
          label={{
            position: "insideTopLeft",
            value: "BPA phase-out",
            fontFamily: "Overpass, sans-serif",
          }}
          x={2007.5}
          stroke="#FF0000"
          strokeWidth={2}
          strokeDasharray="3 3"
        />
      </LineChart>
    </ResponsiveContainer>
  );
}
